import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PredictorReportPage } from './predictor-report.page';

const routes: Routes = [
  {
    path: '',
    component: PredictorReportPage
  },
  {
    path: 'predictor-filter',
    loadChildren: () => import('./predictor-filter/predictor-filter.module').then( m => m.PredictorFilterPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PredictorReportPageRoutingModule {}
